.ecl-card{background-color:#fff;border:0;border-radius:8px;box-shadow:0 2px 4px rgba(9,49,142,.08),0 0 10px rgba(9,49,142,.04),0 4px 5px rgba(9,49,142,.04),0 -4px 4px rgba(9,49,142,.04);box-sizing:border-box;height:100%;margin:0;overflow:hidden;overflow-wrap:break-word;width:100%}.ecl-card__image{-webkit-margin-start:0;-webkit-margin-end:0;aspect-ratio:3/2;border:0 solid #cfdaf5;display:block;height:auto;margin-inline-end:0;margin-inline-start:0;margin-top:0;-o-object-fit:cover;object-fit:cover;width:100%}.ecl-card__body{padding:1.5rem;position:relative}.ecl-card__body:before{background-color:#fc0;border-bottom-left-radius:2px;border-bottom-right-radius:2px;content:"";height:4px;left:1.5rem;position:absolute;top:0;width:32px}